.main-container {
  text-align: center;
  margin: 0 auto;
  margin-bottom: 100px;
}

.editor {
  display: flex;
  justify-content: center;
  text-align: center;
}

h3 {
  padding-top: 20px;
}

.input-primer {
  width: 100%;
  text-align: left;
  margin-bottom: 10px;
}

.input-primer-name {
  float: left;
  margin-bottom: 10px;
}

.input-primer-error {
  color:red;
}

.tf-tree .button {
  background-color: gray; /* Green */
  border: none;
  color: white;
  padding: 4px 10px;
  margin-left: 5px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  border-radius: 4px;
  cursor: pointer;
  /* font-size: 16px; */
}

.tf-nc {
  border-radius: 15px;
}

.tf-tree .giant-button{
  font-size: 16px;
  margin: 10px;
}

.grid-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
  gap: 0px 0px;
  grid-template-areas:
    "main-container main-container closeup-container closeup-container closeup-container"
    "main-container main-container closeup-container closeup-container closeup-container"
    "main-container main-container closeup-container closeup-container closeup-container";
}
.main-container { grid-area: main-container; }
.closeup-container { grid-area: closeup-container; max-width: 900px}

.tf-tree h3 {
  padding-bottom: 20px;
}

p {
  max-width: 600px;
  text-align: center;
  margin: 0 auto;
  padding-bottom: 15px;
  font-size: large;
}

div {
  text-align: center;
}

.collapse-button {
  
  text-align: center;
}

.collapse-content.collapsed {
  display: none;
}

.collapsed-content.expanded {
  display: block;
}
